.Card {
  background-color: white;
  max-width: 350px; /*327px mobile*/
  margin: 0 auto;
  position: relative;
  padding: 80px 0 44px 0;
  border-radius: 5px;
}

/*
.Card:nth-child(1),  .Card:nth-child(3){
    margin-bottom: 92px;
}*/

.card__Icon-Ilustration {
  background-color: var(--dark-violet);
  display: flex;
  width: 88px;
  padding: 23.5px 0;
  margin: 0 auto;
  border-radius: 50%;
  position: absolute;
  top: -43px; /*-43px*/
  left: 36.6%; /*120px*/
  right: 36.6%;
}

.card__Icon-Ilustration img {
  display: block;
  max-width: 100%;
  margin: 0 auto;
  width: 40px;
  height: 40px;
}

.card__Title {
  font-weight: 700;
  font-style: normal;
  color: var(--very-dark-violet);
  font-size: 1.21rem;
  margin-bottom: 20px;
  text-align: center;
}

.card__description {
  font-weight: 500;
  font-style: normal;
  color: var(--grayish-violet);
  font-size: 0.85rem;
  line-height: 1.6;
  text-align: center;
  padding: 0 28px;
}

@media screen and (min-width: 780px) {
  .Card {
    padding: 80px 32px 46px 32px;
  }

  .card__Icon-Ilustration {
    top: -43px; /*-43px*/
    left: 32px; /*120px*/
    right: 0;
    margin: 0;
  }

  .card__Title {
    font-size: 1.24rem;
    margin-bottom: 18px;
    text-align: center;
  }

  .card__description {
    font-size: 0.83rem;
    line-height: 1.6;
    text-align: center;
    padding: 0 28px;
    padding: 0;
  }

  .card__Title,
  .card__description {
    text-align: left;
  }
}
